






















































































































import Vue from 'vue';
import { mapActions, mapState } from 'vuex';

import { add, compare, fixTo2, fixTo8, fromInteger2Float, random10 } from '@/libs';
import ContractList from '@/constants';
import Button from '@/components/framework/Button.vue';

export default Vue.extend({
  name: 'Mining',
  components: {
    Button,
  },
  data: () => ({
    withdrawableReward: '...',
    hasWithdrawable: true,
    isLoaded: false,
    snackbar: false,
    snackbarText: '',
    snackbarTx: '',
    withdrawing: false,

    swapList: [
      {
        input: 'USDT',
        output: 'VNLA',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'WHT',
        output: 'VNLA',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'ETH',
        output: 'VNLA',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'USDT',
        output: 'ETH',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'USDT',
        output: 'HUSD',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'USDT',
        output: 'MDX',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'USDT',
        output: 'WHT',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'WHT',
        output: 'MDX',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'WHT',
        output: 'HUSD',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'WHT',
        output: 'USDT',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
      {
        input: 'WHT',
        output: 'ETH',
        aror: '',
        dror: '',
        volume: '',
        reward: '',
      },
    ],

  }),
  computed: {
    ...mapState('wallet', [
      'accounts',
      'chainId',
      'authorized',
    ])
  },
  methods: {
    ...mapActions({
      connect: 'wallet/connect',
      connectWallet: 'wallet/connectWallet',
      takerWithdraw: 'swap/takerWithdraw',
      getReward: 'swap/getReward',
      getSwapAmount: 'swap/getSwapAmount',
    }),
    redirect(input: string, output: string) {
      setTimeout(() => {
        this.$router.push(
          {
            name: 'Swap',
            query: {
              input,
              output,
            }
          }
        );
      }, 300);
    },
    async tokenReward(input: string): Promise<string> {
      if (this.accounts[0]) {
        return await this.getReward({
          contractAddress: ContractList['swap'][this.chainId],
          userAddress: this.accounts[0],
          tokenAddress: ContractList[this.chainId][input].address,
        });
      } else {
        return '0';
      }
    },
    async tokenSwapAmount(input: string): Promise<string> {
      try {
        if (this.accounts[0]) {
          return await this.getSwapAmount({
            contractAddress: ContractList['swap'][this.chainId],
            userAddress: this.accounts[0],
            tokenAddress: ContractList[this.chainId][input].address,
          });
        } else {
          return '0';
        }
      } catch (e) {
        console.log(e);
        return '0';
      }
    },
    async getWithdrawableAmount(): Promise<string> {
      if (this.accounts[0]) {
        let totalReward = '0';
        for (const token of Object.keys(ContractList[this.chainId])) {
          const reward = await this.tokenReward(token);
          totalReward = add(totalReward, reward);
          console.log(totalReward);
        }
        console.log(compare(totalReward.toString(), '0'));
        if (compare(totalReward.toString(), '0') === 0) {
          this.hasWithdrawable = false;
        }
        return fromInteger2Float(totalReward.toString(), 18);
      } else {
        return '0';
      }
    },
    async withdraw() {
      try {
        await this.takerWithdraw({
          contractAddress: ContractList['swap'][this.chainId]
        }).then(async (response) => {
          this.snackbarText = `${this.$i18n.t('MiningWithdrawSnackbar')}: ${response.hash}`;
          this.withdrawing = true;
          await response.wait(1);
          this.withdrawing = false;

          this.withdrawableReward = fixTo8((await this.getWithdrawableAmount()));

          this.snackbarText = this.$i18n.t('MiningWithdrawSnackbar').toString();
          this.snackbar = true;
        });
      } catch (e) {
        if (e.data) {
          if (e.data.code === 3) {
            this.snackbarText = this.$i18n.t('MiningLockSnackbar').toString();
            this.snackbar = true;
            this.withdrawing = false;
          }
        }
        console.error(e);
      }
    }
  },
  async mounted() {
    await this.connect();
    await this.connectWallet();

    for (const index in this.swapList) {
      const input = this.swapList[index].input;
      const output = this.swapList[index].output;
      this.swapList[index]['aror'] = fixTo2(random10(ContractList['pairs'][input][output].interests[0].toString()));
      this.swapList[index]['dror'] = fixTo2(random10(ContractList['pairs'][input][output].interests[1].toString()));
      this.swapList[index]['volume'] = fixTo8(fromInteger2Float(await this.tokenSwapAmount(input), ContractList[this.chainId][input]).slice(0, 6));
      this.swapList[index]['reward'] = fixTo8(fromInteger2Float(await this.tokenReward(input), 18));
    }

    this.withdrawableReward = fixTo8((await this.getWithdrawableAmount()));
    this.isLoaded = true;
  }
});
